header {
    display: flex;
    align-items: center;
  }
  
  header h1 {
    margin-right: 20px;
  }
  
  .header-logo {
    width: 50px; /* Adjust the width as needed */
    height: auto;
  }

#filters {
    margin-top: 20px;
    padding: 10px;
    border: 1px solid #ddd;
    background-color: #f9f9f9;
  }
  
  #brand-list,
  #price-range {
    margin-bottom: 20px;
  }

  #filters .ais-SearchBox {
    display: none;
  }
  
  .ais-RangeSlider {
    padding: 10px;
  }
  
  .ais-RangeSlider--horizontal {
    width: 100%;
  }
  
  .ais-RangeSlider--horizontal .ais-RangeSlider-handle {
    background-color: #007bff;
  }
  
  .ais-RangeSlider--horizontal .ais-RangeSlider-track {
    background-color: #ddd;
  }
  